import React, { useState } from "react";
import { Head } from "../../Components";
import { Container, DivRowCenter, Title, DivRow } from "./Styles";

function About() {
  return (
    <>
      <Head />
      <Container>
        <DivRowCenter>
          <Title>Sobre mí</Title>
        </DivRowCenter>
        <DivRowCenter>
          <DivRow>
            <div style={{ width: "35%" }}></div>
            <div style={{ width: "65%" }}>
              <h2 style={{ color: "#ffffff" }}>Hola</h2>
              <p
                style={{
                  color: "#ffffff",
                  textAlign: "justify",
                  marginRight: 40,
                  fontSize: 22
                }}
              >
                Casi todo el tiempo desde que egrese de la universidad
                tecnologica de xicotepec de juarez me dedique al desarrollo web
                empezando en lenguajes del lado del servidor como vb.net, c# ,
                php y java, gestionando igual bases de datos en sql server y
                mysql. Actalmente taengo mas de 5 años de experiencia en el
                desarrollo. Igual me gusta a aprender nuevas tecnologias y
                enseñar a mis compañeros o amigos que requieran de mi ayuda
              </p>
            </div>
          </DivRow>
        </DivRowCenter>
      </Container>
    </>
  );
}

export default About;
